import React from 'react';
import { StyleSheet, View, Text, TouchableOpacity, SafeAreaView } from 'react-native'; 
import { Actions } from 'react-native-router-flux';
import BG_SVG from './../../assets/bgSvg';

const Welcome = props => {

    return (
        <SafeAreaView style={styles.wrapper}>
            <BG_SVG />
            <View style={styles.bottom}>
                <Text style={{color: '#fff', fontSize: 48, fontFamily: 'monospace', marginTop: 70}}>WELCOME</Text>
                <View>
                    <TouchableOpacity onPress={Actions.login} style={styles.button}>
                        <View style={styles.loginButton}>
                            <Text style={{color: '#fff'}}>LOGIN</Text>
                        </View>
                    </TouchableOpacity>
                    <TouchableOpacity onPress={Actions.register} style={styles.button}>
                        <View style={styles.registerButton}>
                            <Text style={{color: '#fff'}}>REGISTER</Text>
                        </View>
                    </TouchableOpacity>
                </View>
            </View>
        </SafeAreaView>
    )
}

const styles = StyleSheet.create({
    wrapper: {
        flex: 1, 
        flexDirection: 'column'
    },
    bottom: {
        flex: 1,
        flexDirection: 'column',
        paddingLeft: 20,
        paddingRight: 20,
        paddingBottom: 40,
        justifyContent: 'space-between',
        alignItems: 'center'
    },
    button: {
        width: 250,
        marginTop: 20,
        height: 50,
        borderRadius: 25,
        overflow: 'hidden',
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 5,
        },
        shadowOpacity: 0.3,
        shadowRadius: 6.68,
        elevation: 11,
    },
    loginButton: {
        flex: 1, 
        justifyContent: 'center', 
        alignItems: 'center', 
        backgroundColor: '#5e0fdb'
    },
    registerButton: {
        flex: 1, 
        justifyContent: 'center', 
        alignItems: 'center', 
        backgroundColor: '#3c0996'
    }
});

